//* FONCTION BOUTTON REFRESH *//

function ajouterBoutonRefresh() {
	// Cr√©er un bouton
	var bouton = document.createElement("button");

	// Ajouter une classe au bouton
	bouton.classList.add("boutonRefresh");

	bouton.textContent = "Nouvelle recherche";

	// Ajouter un gestionnaire d'√©v√©nements pour le clic sur le bouton
	bouton.addEventListener("click", function () {
		maFonction();
	});

	// Ajouter le bouton au corps du document
	document.body.appendChild(bouton);
}

// La fonction √† ex√©cuter lorsque le bouton est cliqu√©
function maFonction() {
	if (window.confirm("√ätes-vous s√ªr de vouloir quitter la page ?")) {
		window.location.reload();
	} else {
		alert("Op√©ration annul√©e !");
	}
}

module.exports = ajouterBoutonRefresh;
